const db = wx.cloud.database()
export default {
	data() {
		return {
			list: [],
		};
	},

	methods: {
		async getList() {
			try{
				let res = await db.collection('history').get()
				console.log(res)
				this.list = res.data
			}catch(e){
				console.log(e)
			}
		}
	},

	onLoad() {
		this.getList()
	}
}
